
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { Recommendation } from './recommendation';

@Injectable()
export class RecommendationService {

  private recommendationsUrl: string = 'http://localhost:8080/rec';

  constructor(public http: HttpClient) { }

  public findAll(): Observable<Recommendation[]> {
    return this.http.get<Recommendation[]>(this.recommendationsUrl);
  }

  public save(recommendation: Recommendation) {
    return this.http.post<Recommendation>(this.recommendationsUrl, recommendation);
  }
}



